<template>
  <div style="text-align: center;">
    <el-switch
      style="display: block;margin-top: 10px;"
      v-model="value2"
      active-color="#13ce66"
      inactive-color="#b9b9b9"
      inactive-text="开 关">
    </el-switch>
    <div style="margin-top: 20px;">
      <el-tag style="font-size: 12px;">本地RPC服务器端口</el-tag>
      <el-input v-model="port" placeholder="请输入端口" size='small' style="width: 100px;" ></el-input>
      <p><el-tag type="info">aria2服务器仅支持服务器本地，输入您服务器本地aria2服务器端口即可，默认为6800</el-tag></p>
    </div>
    <div style="margin-top: 20px;">
      <el-tag style="font-size: 12px;">RPC Secret</el-tag>
      <el-input v-model="secret" placeholder="请输入授权令牌" size='small' style="width: 150px;" ></el-input>
      <p><el-tag type="info">RPC授权令牌，与 Aria2 配置文件中保持一致，未设置请留空</el-tag></p>
    </div>
    <div style="margin-top: 20px;">
      <el-tag style="font-size: 12px;">临时目录</el-tag>
      <el-input v-model="tmpdownpath" placeholder="请输入临时目录" size='small' style="width: 150px;" ></el-input>
      <p><el-tag type="info">临时目录的绝对路径，程序需要此目录的读、写、执行权限。</el-tag></p>
    </div>
    <div style="margin-top: 20px;">
      <el-tag style="font-size: 12px;">刷新间隔(秒)</el-tag>
      <el-input v-model="time" placeholder="请输入间隔时间" size='small' style="width: 150px;" ></el-input>
      <p><el-tag type="info">向Aria2请求刷新任务状态的间隔</el-tag></p>
    </div>
    <el-button @click="save" round>保存</el-button>
    <el-button @click="testconnect" round>测试连接</el-button>
  </div>
</template>

<script>
export default{
  data () {
    return {
      value2: false,
      port: '',
      tmpdownpath: '',
      secret: '',
      time: ''
    }
  },
  methods: {
    save () {
      var pp = new FormData()
      pp.append('Status', this.value2 === false ? 'no' : 'yes')
      pp.append('Port', this.port)
      pp.append('TmpDownPath', this.tmpdownpath)
      pp.append('Secret', this.secret)
      pp.append('Time', this.time)
      this.$axios({
        method: 'POST',
        url: '/aria2config',
        headers: {
          'Content-Type': 'multipart/form-data'
        },
        data: pp
      }).then(
        res => {
          if (res.status === 200) {
            this.$message.success('保存成功')
          } else {
            this.$message.error('保存失败')
          }
        }
      )
    },
    testconnect () {
      this.$axios.get('/aria2test').then(res => {
        if (res.data !== 'failed') {
          this.$message.success('连接成功，aria2版本：' + res.data.Version)
        } else {
          this.$message.error('连接失败')
        }
      })
    }
  },
  beforeMount () {
    this.$axios.get('/aria2config').then(res => {
      this.value2 = res.data.Status === 'yes'
      this.port = res.data.Port
      this.time = res.data.Time
      this.secret = res.data.Secret
      this.tmpdownpath = res.data.TmpDownPath
    })
  }
}
</script>

<style>
</style>



