<template>
  <div>
    <el-container>
      <el-header style="height: 35px;">
        <div style="float: left;" v-for="(path,index) in route" :key="index" ><el-button plain  style="border: none;" @click="go_route(index)" >{{path}}</el-button><i class="el-icon-arrow-right"></i></div>
      </el-header>
      <el-scrollbar style="height: 85vh;">
      <el-main name="test" v-loading="loading">
        <div @contextmenu.prevent="mainleft" @click="mainright" style="height: 82vh;">
        <el-divider content-position="left" v-if="dir_view" ><h3 style="margin: 0;">文件夹</h3></el-divider>
        <el-row :gutter="8">
          <el-col :xs="12" :sm="8" :md="6" :lg="4" :xl="2" v-for="(item,key,i) in dir" :key="i" >
            <el-tooltip class="item" effect="dark" :content="'日期：' + getLocalTime(item.DataTime)" placement="top">
              <div @contextmenu.stop.prevent="yyyy($event,item)">
                <el-button style="width: 100%;text-align: left;padding: 15px;overflow: hidden;text-overflow: ellipsis;"  icon="iconfont icon-wenjian3" @click="enter(item['DataName'])" >
                {{item['DataName']}}
                </el-button>
              </div>
            </el-tooltip>
          </el-col>
        </el-row>
        <div style="margin-top: 18px;">
          <el-divider content-position="left" v-if="file_view" ><h3 style="margin: 0;">文件</h3></el-divider>
          <el-checkbox-group v-model="checkList">
          <el-row :gutter="8">
            <el-col :xs="8" :sm="6" :md="6" :lg="4" :xl="2" v-for="(item,key,i) in file" :key="i" >
              <el-tooltip class="item" effect="dark" placement="top">
                <div slot="content">大小：{{size(item.DataSize)}}<br/>日期：{{getLocalTime(item.DataTime)}}</div>
              <div @contextmenu.stop.prevent = "yyyy($event,item)" >
              <el-card shadow="always" :body-style="{ padding: '10px' }" style="border-radius: 20px;">
                <el-checkbox style="width: 100%;height:0;padding-bottom:100%;" :label="item.DataFileId" >
                    <div ref="pageContainer" :style="{width: '100%', height: autoheight}">
                      <el-image
                        style="width: 100%;height: 100%;border-radius: 10px;"
                        :src= "getthu(item.DataFileId)"
                        fit="cover"
                        v-if = "judgetype(item.DataName) == 'picture'"
                        :preview-src-list="srcList"
                        >
                      </el-image>
                      <el-image
                        style="width: 100%;height: 100%;border-radius: 10px;"
                        :src= "tubiao(item.DataName)"
                        fit="fill"
                        v-if = "judgetype(item.DataName) != 'picture'"
                        >
                      </el-image>
                      <div style="text-align: center;margin-top: 5px;overflow: hidden;text-overflow: ellipsis;">{{item['DataName']}}</div>
                    </div>
                </el-checkbox>
              </el-card>
              </div>
              </el-tooltip>
            </el-col>
          </el-row>
          </el-checkbox-group>
        </div>
        <div class="right-menu"
            v-show="contextMenuVisible"
            :style="{top:top + 'px',left:left + 'px'}"
            >
            <a href="javascript:;" @click="flase" ><i class="iconfont icon-shuaxin" style="margin-left: 5px;margin-right: 5px;"/>刷新页面</a>
            <a href="javascript:;" @click="()=>{dialogVisible = true;contextMenuVisible = false}" ><i class="iconfont icon-tianjiawenjian" style="margin-left: 5px;margin-right: 5px;" />创建文件夹</a>
            <a href="javascript:;" @click="contextMenuVisible = false" >
              <el-upload
                class="upload-demo"
                action="/api/file/"
                multiple
                :file-list = "files"
                :data = "paths_"
                :before-upload = 'uploadpath'
                :show-file-list = 'false'
                :on-progress = 'hello_'
                >
              <i class="iconfont icon-shangchuan" style="margin-left: 5px;margin-right: 5px;" />上传文件
              </el-upload>
            </a>
            <a href="javascript:;" @click="()=>{dialogVisible_ = true;contextMenuVisible = false}"><i class="iconfont icon-xiazai_2" style="margin-left: 5px;margin-right: 5px;" />离线下载</a>
        </div>
        <div class="right-menu"
            v-if="contextMenuVisible_"
            :style="{top:top_ + 'px',left:left_ + 'px'}"
            >
          <a href="javascript:;" @click="playvideo" v-if="judgetype(rename_file.OldName) === 'video'" ><i class="el-icon-video-camera-solid" style="margin-left: 5px;margin-right: 5px;"/>播放</a>
          <a href="javascript:;" @click="uncompressbutton" v-if="judgetype(rename_file.OldName) === 'compress'" ><i class="el-icon-arrow-up" style="margin-left: 5px;margin-right: 5px;"/>解压</a>
          <a href="javascript:;" @click="filedown" ><i class="iconfont icon-xiazai_2" style="margin-left: 5px;margin-right: 5px;"/>下载</a>
          <a href="javascript:;" @click="movebutton" ><i class="el-icon-position" style="margin-left: 5px;margin-right: 5px;"/>移动</a>
          <a href="javascript:;" @click="dialogVisible__ = true" ><i class="el-icon-edit" style="margin-left: 5px;margin-right: 5px;"/>重命名</a>
          <a href="javascript:;" @click="deletefile" ><i class="el-icon-delete" style="margin-left: 5px;margin-right: 5px;"/>删除</a>
        </div>
        <uploader ref='uploader' @file-success='hhhh' @file-added="fileprogress" :options="options" class="uploader-example">
          <uploader-unsupport></uploader-unsupport>
          <uploader-list>
            <div slot-scope="props">
            <el-dialog
              title="上传列表"
              :visible.sync="downvisible"
              center
              @closed="flase"
              >
              <div v-for="file in props.fileList" :key="file.id">
                <uploader-file :file="file" :list="true"></uploader-file>
              </div>
              <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="downvisible = false">确 定</el-button>
              </span>
            </el-dialog>
            </div>
          </uploader-list>
            <uploader-btn style="border: none;border-radius: 50%;position:fixed;right: 20px;bottom: 20px;">
              <i class="iconfont icon-upload-fill" style="font-size: 25px;"></i>
            </uploader-btn>
        </uploader>
        <el-dialog
          title="提示"
          :visible.sync="dialogVisible"
          center
          >
          <el-input v-model="input" placeholder="请输入文件夹名"></el-input>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="createdir">确 定</el-button>
            <el-button @click="dialogVisible = false">取 消</el-button>
          </span>
        </el-dialog>
        <el-dialog
          :visible.sync="dialogVisible_"
          center
          >
          <el-input v-model="input_" placeholder="请输入链接(支持http/磁链)"></el-input>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="dialogVisible_ = false">确 定</el-button>
            <el-button @click="dialogVisible_ = false">取 消</el-button>
          </span>
        </el-dialog>
        <el-dialog
          title="重命名"
          :visible.sync="dialogVisible__"
          center
          >
          <el-input v-model="rename_file.NewName" placeholder="请输入新名字"></el-input>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="rename">确 定</el-button>
            <el-button @click="dialogVisible__ = false">取 消</el-button>
          </span>
        </el-dialog>
        <el-dialog
          title="移动至"
          :visible.sync="movevisible"
          center
          >
          <el-row :gutter="10" v-loading="loading_">
            <el-col :xs="24" :sm="12" :md="8" :lg="6" :xl="4" v-for="(item,i) in movelist" :key="i" >
              <el-button style="width: 100%;" @click="enterlist(item.DataName)">{{item.DataName}}</el-button>
            </el-col>
          </el-row>
          <span slot="footer" class="dialog-footer">
            <div style="margin: 5px;text-align: left;">
              <el-tag type="info">目录:{{movepath_}}</el-tag>
            </div>
            <el-button type="primary" @click="moveaction">确 定</el-button>
            <el-button @click="movevisible = false">取 消</el-button>
          </span>
        </el-dialog>
        <el-dialog
          title="解压至"
          :visible.sync="uncompressvisible"
          center
          >
          <el-row :gutter="10" v-loading="loading__">
            <el-col :xs="24" :sm="12" :md="8" :lg="6" :xl="4" v-for="(item,i) in movelist" :key="i" >
              <el-button style="width: 100%;" @click="enterlist(item.DataName)">{{item.DataName}}</el-button>
            </el-col>
          </el-row>
          <span slot="footer" class="dialog-footer">
            <div style="margin: 5px;text-align: left;">
              <el-input v-model="PassWord" placeholder="请输入解压密码(仅支持RAR)"></el-input>
            </div>
            <div style="margin: 5px;text-align: left;">
              <el-tag type="info">目录:{{movepath_}}</el-tag>
            </div>
            <el-button type="primary" @click="uncompress">确 定</el-button>
            <el-button @click="uncompressvisible = false">取 消</el-button>
          </span>
        </el-dialog>
        </div>
        </el-main>
      </el-scrollbar>
    </el-container>
  </div>
</template>

<script>
import $ from 'jquery'
const video = ['wav', 'avi', 'mov', 'mp4', 'flv', 'rmvb', 'mpeg', 'mpg', 'mkv']
const compress = ['rar', 'zip', '7z', 'gz', 'tar', 'gz']
const picture = ['jpg', 'jpeg', 'bmp', 'gif', 'png', 'tif']
const sound = ['mp3', 'wma', 'mmf', 'flac', 'acc', 'amr']
const Base64 = require('js-base64').Base64
export default{
  data () {
    return {
      autoheight: '',
      checkList: [],
      datas: [],
      radio1: [],
      route: ['/'],
      dir_view: false,
      file_view: false,
      dir: [],
      file: [],
      files: [],
      paths_: {
        path: '/'
      },
      contextMenuVisible: false,
      contextMenuVisible_: false,
      dialogVisible: false,
      input: '',
      dialogVisible_: false,
      input_: '',
      loading: true,
      downvisible: false,
      fileuploadlist: {},
      top: 0,
      left: 0,
      top_: 0,
      left_: 0,
      delete_file: {
        FileID: '',
        Path: '',
        FileName: '',
        Type: ''
      },
      rename_file: {
        OldName: '',
        NewName: '',
        Path: '',
        FileID: '',
        Type: '',
        Email: ''
      },
      dialogVisible__: false,
      movevisible: false,
      movelist: [],
      movepath: [],
      movepath_: '/',
      loading_: false,
      PassWord: '',
      uncompressvisible: false,
      loading__: false,
      options: {
        target: this.getuploadaddress,
        testChunks: false,
        headers: this.hello,
        uploadMethod: 'PUT',
        method: 'octet',
        chunkSize: 1024 * 320 * 160,
        simultaneousUploads: 1,
        maxChunkRetries: 3,
        forceChunkSize: true
      },
      urls: {},
      uppath: '/',
      srcList: [],
      imgs: {
        video: require('../assets/文件类型 - 视频文件.png'),
        compress: require('../assets/文件类型 - 压缩文件.png'),
        music: require('../assets/文件类型 - 音乐.png'),
        word: require('../assets/文件类型 - word.png'),
        ppt: require('../assets/文件类型 - ppt.png'),
        pdf: require('../assets/文件类型 - PDF.png'),
        excel: require('../assets/文件类型 - excel.png'),
        none: require('../assets/文件类型 - 未识别文件占位图.png'),
        text: require('../assets/文件类型 - 文本文档.png'),
        exe: require('../assets/文件类型 - exe执行文件.png')
      }
    }
  },
  watch: {
    $route (to, from) {
      let path = to.query.path
      if (path === '/') {
        this.$axios.get('/file/' + '%2F').then(
          response => {
            if (response.data !== null) {
              this.datas = response.data
            } else {
              this.datas = []
            }
            this.dir = []
            this.file = []
            this.fenlei()
            this.loading = false
          }
        )
      } else {
        this.route = path.split('/')
        this.route[0] = '/'
        let url = ''
        for (let j = 1; j < this.route.length; j++) {
          url += '%2F' + this.route[j]
        }
        this.$axios.get('/file/' + url).then(
          response => {
            if (response.data !== null) {
              this.datas = response.data
            } else {
              this.datas = []
            }
            this.dir = []
            this.file = []
            this.fenlei()
            this.loading = false
          }
        )
      }
    }
  },
  methods: {
    tubiao (name) {
      let index = name.lastIndexOf('.')
      let extend = name.substr(index + 1).toLowerCase()
      if (index !== -1) {
        if (video.indexOf(extend) > -1) {
          return this.imgs.video
        } else if (compress.indexOf(extend) > -1) {
          return this.imgs.compress
        } else if (sound.indexOf(extend) > -1) {
          return this.imgs.music
        } else if (extend === 'doc' || extend === 'docx') {
          return this.imgs.word
        } else if (extend === 'pdf') {
          return this.imgs.pdf
        } else if (extend === 'ppt' || extend === 'pptx') {
          return this.imgs.ppt
        } else if (extend === 'xls' || extend === 'xlsx') {
          return this.imgs.excel
        } else if (extend === 'exe') {
          return this.imgs.exe
        } else if (extend === 'txt') {
          return this.imgs.text
        } else {
          return this.imgs.none
        }
      } else {
        return this.imgs.none
      }
    },
    fileprogress (...values) {
      this.downvisible = true
    },
    getthu (id) {
      let url = '/api/thumbnail/' + id
      return url
    },
    uploadpath_: function () {
      if (this.route.length > 1) {
        let url = ''
        for (let j = 1; j < this.route.length; j++) {
          url += '/' + this.route[j]
        }
        this.uppath = url
      } else {
        this.uppath = '/'
      }
      return this.uppath
    },
    hello: function (...values) {
      var next = values[1].endByte - 1
      var tmp = {
        'Content-Range': 'bytes ' + values[1].startByte + '-' + next + '/' + values[0].size
      }
      return tmp
    },
    getuploadaddress: function (...values) {
      let datas
      let address
      if (values[1].offset === 0) {
        let params = new FormData()
        let ppp = this.uploadpath_()
        params.append('path', ppp)
        params.append('filename', values[0].name)
        params.append('filesize', values[0].size)
        $.ajax({
          url: '/api/uploadaddress',
          type: 'POST',
          async: false,
          data: params,
          contentType: false,
          processData: false,
          success: function (backData) { datas = backData }
        })
        let id = 's' + values[0].id
        this.urls[id] = {
          Address: datas.Address,
          StoreID: datas.StoreID,
          Path: this.uploadpath_(),
          Name: values[0].name,
          Size: parseInt(values[0].size / 1024),
          Type: 'file',
          ItemID: ''
        }
        address = this.urls[id].Address
      } else {
        let id = 's' + values[0].id
        address = this.urls[id].Address
      }
      return address
    },
    hhhh: function (...values) {
      let recall = JSON.parse(values[2])
      let id = 's' + values[0].id
      this.urls[id].ItemID = recall.id
      this.$axios({
        method: 'POST',
        data: this.urls[id],
        url: '/onedrivestoredata'
      }).then(res => {
        if (res.data === 'succeed') {
          this.$message.success('上传成功')
        } else {
          this.$message.error('上传失败')
        }
      })
    },
    getLocalTime: function (nS) {
      return new Date(nS * 1000).toLocaleString().replace(/:\d{1,2}$/, ' ')
    },
    uncompress () {
      let tmp = {
        FileID: this.rename_file.FileID,
        NewPath: this.movepath_,
        PassWord: this.PassWord
      }
      this.$axios({
        method: 'PUT',
        url: '/UnArchive',
        data: tmp
      }).then(res => {
        if (res.data === 'succeed') {
          this.$message.success('解压任务提交成功')
        } else {
          this.$message.error('解压任务提交失败')
        }
        this.flase()
        this.uncompressvisible = false
      })
    },
    uncompressbutton () {
      this.loading__ = true
      let tmp = []
      this.movepath = []
      this.movelist = []
      this.movepath_ = '/'
      this.$axios.get('/file/' + '%2F').then(
        response => {
          if (response.data !== null) {
            tmp = response.data
          } else {
            tmp = []
          }
          for (var item of tmp) {
            if (item['DataType'] === 'dir') {
              this.movelist.push(item)
            }
          }
          this.loading__ = false
        }
      )
      this.uncompressvisible = true
    },
    moveaction () {
      let tmp = {
        FileID: this.rename_file.FileID,
        FileName: this.rename_file.OldName,
        OldPath: this.rename_file.Path,
        NewPath: '',
        Type: this.rename_file.Type
      }
      tmp.NewPath = this.movepath_
      this.$axios({
        method: 'PUT',
        url: '/move/',
        data: tmp
      }).then(res => {
        if (res.data === 'succeed') {
          this.$message.success('移动成功')
        } else {
          this.$message.error('移动失败')
        }
        this.flase()
        this.movevisible = false
      })
    },
    enterlist (Name) {
      this.loading_ = true
      let tmp = []
      this.movelist = []
      this.movepath.push(Name)
      this.movepath_ = ''
      for (var path of this.movepath) {
        this.movepath_ += '/' + path
      }
      this.$axios.get('/file/' + encodeURIComponent(this.movepath_)).then(
        response => {
          if (response.data !== null) {
            tmp = response.data
          } else {
            tmp = []
          }
          for (var item of tmp) {
            if (item['DataType'] === 'dir') {
              this.movelist.push(item)
            }
          }
          this.loading_ = false
        }
      )
    },
    movebutton () {
      this.loading_ = true
      let tmp = []
      this.movepath = []
      this.movelist = []
      this.movepath_ = '/'
      this.$axios.get('/file/' + '%2F').then(
        response => {
          if (response.data !== null) {
            tmp = response.data
          } else {
            tmp = []
          }
          for (var item of tmp) {
            if (item['DataType'] === 'dir') {
              this.movelist.push(item)
            }
          }
          this.loading_ = false
        }
      )
      this.movevisible = true
    },
    playvideo () {
      var url = '/api/filedown/' + this.rename_file.FileID
      this.$router.push({path: 'video/' + Base64.encode(url)})
    },
    size (value) {
      var unit = ['KB', 'MB', 'GB', 'TB']
      var i = 0
      while (value >= 1024) {
        value = value / 1024
        i++
      }
      return (value.toFixed(2) + unit[i])
    },
    rename: function () {
      this.$axios({
        method: 'PUT',
        url: '/file',
        data: this.rename_file
      }).then((res) => {
        if (res.data === 'succeed') {
          this.$message.success('重命名成功')
        } else {
          this.$message.error('重命名失败')
        }
        this.flase()
      })
      this.dialogVisible__ = false
    },
    deletefile () {
      var url_
      if (this.delete_file.Type === 'file') {
        url_ = '/files/' + this.delete_file.FileID
      } else {
        url_ = '/dir/' + this.delete_file.FileID
      }
      this.$axios({
        method: 'DELETE',
        url: url_
      }).then((res) => {
        if (res.data === 'succeed') {
          this.$message.success('删除成功')
        } else {
          this.$message.error('删除失败')
        }
        this.flase()
        this.delete_file = {
          FileID: [],
          Path: [],
          FileName: [],
          Type: []
        }
      })
      this.contextMenuVisible_ = false
    },
    yyyy (v, item) {
      this.top_ = v.y
      this.left_ = v.x
      this.delete_file.FileID = item.DataFileId
      this.delete_file.Path = item.DataPath
      this.delete_file.FileName = item.DataName
      this.delete_file.Type = item.DataType
      this.rename_file.OldName = item.DataName
      this.rename_file.Email = 'admin@godcloud.com'
      this.rename_file.FileID = item.DataFileId
      this.rename_file.Type = item.DataType
      this.rename_file.Path = item.DataPath
      this.rename_file.NewName = item.DataName
      this.contextMenuVisible = false
      this.contextMenuVisible_ = true
    },
    mainleft (v) {
      this.contextMenuVisible = true
      this.contextMenuVisible_ = false
      this.top = v.y
      this.left = v.x
    },
    mainright: function () {
      this.contextMenuVisible_ = false
      this.contextMenuVisible = false
    },
    format (percentage) {
      return percentage === 100 ? '完成' : `当前进度:${percentage}%`
    },
    judgetype: function (name) {
      let index = name.lastIndexOf('.')
      let extend = name.substr(index + 1)
      if (index !== -1) {
        if (video.indexOf(extend) > -1) {
          return 'video'
        } else if (compress.indexOf(extend) > -1) {
          return 'compress'
        } else if (picture.indexOf(extend) > -1) {
          return 'picture'
        } else if (sound.indexOf(extend) > -1) {
          return 'sound'
        } else {
          return 'file'
        }
      } else {
        return 'file'
      }
    },
    icon: function (name) {
      let type = this.judgetype(name)
      if (type !== 'file') {
        if (type === 'video') {
          return 'el-icon-video-camera'
        } else if (type === 'compress') {
          return 'el-icon-box'
        } else if (type === 'picture') {
          return 'iconfont icon-tupian'
        } else if (type === 'sound') {
          return 'iconfont icon-yinle'
        } else {
          return 'iconfont icon-wenjian_2'
        }
      } else {
        return 'iconfont icon-wenjian_2'
      }
    },
    enter: function (path) {
      this.loading = true
      this.route.push(path)
      this.$router.push('home?path=' + this.pathencode())
    },
    go_route: function (num) {
      if (this.route.length - 1 === num) {
        this.flase()
        return
      }
      this.loading = true
      if (num > 0) {
        this.route = this.route.slice(0, num + 1)
        this.$router.push('home?path=' + this.pathencode())
      } else {
        this.route = ['/']
        this.$router.push('home?path=/')
      }
    },
    fenlei: function () {
      this.dir_view = false
      this.file_view = false
      this.srcList = []
      for (var item of this.datas) {
        if (item['DataType'] === 'dir') {
          this.dir_view = true
          this.dir.push(item)
        } else {
          this.file_view = true
          this.file.push(item)
          if (this.judgetype(item.DataName) === 'picture') {
            this.srcList.push(this.getthu(item.DataFileId))
          }
        }
      }
    },
    pathencode: function () {
      let url = ''
      for (let j = 1; j < this.route.length; j++) {
        url += '%2F' + this.route[j]
      }
      return url
    },
    filedown: function () {
      window.location.href = '/api/filedown/' + this.rename_file.FileID
    },
    uploadpath: function () {
      this.downvisible = true
      if (this.route.length > 1) {
        this.paths_['path'] = this.pathencode()
      } else {
        this.paths_['path'] = '/'
      }
    },
    flase: function () {
      this.loading = true
      if (this.route.length === 1) {
        this.$axios.get('/file/%2F').then(
          response => {
            if (response.data !== null) {
              this.datas = response.data
            } else {
              this.datas = []
            }
            this.dir = []
            this.file = []
            this.fenlei()
            this.loading = false
          }
        )
      } else {
        this.$axios.get('/file/' + this.pathencode()).then(
          response => {
            if (response.data !== null) {
              this.datas = response.data
            } else {
              this.datas = []
            }
            this.fenlei()
            this.dir = []
            this.file = []
            this.fenlei()
            this.loading = false
          }
        )
      }
      this.contextMenuVisible = false
    },
    hello_: function (event, file, fileList) {
      let tmp = {
        name: file.name,
        percent: parseFloat(event.percent.toFixed(2))
      }
      this.$set(this.fileuploadlist, file.name, tmp)
    },
    createdir: function () {
      let tmp
      if (this.route.length > 1) {
        tmp = this.pathencode()
      } else {
        tmp = '/'
      }
      let params = new FormData()
      params.append('path', tmp)
      params.append('name', this.input)
      this.$axios({
        url: '/dir',
        method: 'post',
        data: params,
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then(response => {
        this.dialogVisible = false
        this.flase()
        if (response.data === 'succeed') {
          this.$message({
            message: '创建成功',
            type: 'success'
          })
        } else {
          this.$message.error({
            message: '创建失败'
          })
        }
      })
    }
  },
  beforeMount () {
    document.title = '文件管理'
    let tmp = this.$route.query.path
    var par = encodeURIComponent(tmp)
    if (tmp == null) {
      this.$router.push('home?path=/')
    } else {
      if (tmp.length > 1) {
        this.route = tmp.split('/')
      }
      this.route[0] = '/'
      this.$axios.get('/file/' + par).then(
        response => {
          if (response.data !== null) {
            this.datas = response.data
            for (var item of this.datas) {
              if (item['DataType'] === 'dir') {
                this.dir_view = true
                this.dir.push(item)
              } else {
                this.file_view = true
                this.file.push(item)
                if (this.judgetype(item.DataName) === 'picture') {
                  this.srcList.push(this.getthu(item.DataFileId))
                }
              }
            }
          }
          this.loading = false
        }
      )
    }
  },
  updated () {
    if (this.$refs.pageContainer != null) {
      this.autoheight = this.$refs.pageContainer[0].clientWidth + 'px'
    }
  }
}
</script>

<style>
.el-checkbox__label{
  display:block;
  padding-right: 10%;
  padding-left: 10%;
}
.el-col {
    border-radius: 4px;
    margin-top: 8px;
  }
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.el-main{
  padding: 5px;
}
.el-divider{
  margin-top: 5px;
  margin-bottom: 10px ;
}
.right-menu {
  position: fixed;
  background: #fff;
  border: solid 1px rgba(0, 0, 0, .2);
  border-radius: 3px;
  z-index: 999;
}
.right-menu a {
  height: 28px;
  line-height: 28px;
  text-align: left;
  display: block;
  color: #1a1a1a;
}
.right-menu a:hover {
  background: #eee;
  color: #fff;

}
html,
body {
    height: 100%;
}
.right-menu {
    border: 1px solid #eee;
    box-shadow: 0 0.5em 1em 0 rgba(0,0,0,.1);
    border-radius: 3px;
}
a {
    text-decoration: none;
}
.right-menu a {
    padding: 5px;
    border-radius: 3px;

}
.right-menu a:hover {
    background: #42b983;
}
.el-scrollbar__wrap{
  overflow-x: hidden;
}
</style>



