<template>
  <div>
    <el-scrollbar style="height: 90vh;">
    <el-timeline :reverse="true">
        <el-timeline-item style="margin-right: 20px;" v-for="(item,index) in aria2process" :timestamp="getLocalTime(item.BeginTime)" :key="index" placement="top">
          <el-card>
            <p>状态：{{status(item)}}</p>
            <p>文件数：{{item.FileNums}}</p>
            <p>下载速度：{{size(item.DownloadSpeed)}}</p>
            <p>下载目录：{{item.Path}}</p>
            <el-collapse>
              <el-collapse-item title="文件详情">
                <div v-for="(item_,index_) in item.Files" :key="index_" style="margin-top: 30px;">
                  <p>文件名：{{item_.path}}</p>
                  <div>
                    <span>进度：</span>
                    <div class="bar">
                      <el-progress :text-inside="true" :stroke-width="15" :percentage="getpercentnge(item_.completedLength,item_.length)" color="#32CD32"></el-progress>
                    </div>
                    {{size_(item_.length)}}
                  </div>
                </div>
              </el-collapse-item>
            </el-collapse>
            <el-button @click="taskcancel(item)" size="mini" style="position: absolute;top: 30px;right: 5px;" type="danger" icon="el-icon-delete" circle></el-button>
            <el-button @click="taskprocess(item)" size="mini" v-show="item.Status == 'active' || item.Status == 'paused'" style="position: absolute;top: 80px;right: 5px;" type="success" :icon="icons(item)" circle></el-button>
            <el-progress :stroke-width="15" style="position: absolute;top: 40px;right: 40px;" type="circle" :percentage="getpercentnge(item.CompletedLength,item.TotalLength)" color="#00aaff"></el-progress>
          </el-card>
        </el-timeline-item>
      </el-timeline>
    <el-dialog
      :visible.sync="dialogVisible1"
      center
      >
      <el-input
        type="textarea"
        :rows="6"
        placeholder="请输入链接(一行一个链接)"
        v-model="textarea"
        >
      </el-input>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="aria2down">确 定</el-button>
        <el-button @click="dialogVisible1 = false">取 消</el-button>
      </span>
    </el-dialog>
    <el-button @click="dialogVisible1=!dialogVisible1" style="position:fixed;right: 20px;bottom: 20px;" type="warning" icon="el-icon-link" circle></el-button>
    </el-scrollbar>
  </div>
</template>

<script>
export default{
  data () {
    return {
      dialogVisible1: false,
      dialogVisible2: false,
      textarea: '',
      path: '/',
      aria2process: [],
      customColor: [
        {color: '#f56c6c', percentage: 20},
        {color: '#e6a23c', percentage: 40},
        {color: '#e3e300', percentage: 60},
        {color: '#a3fa30', percentage: 80},
        {color: '#3fd658', percentage: 100}
      ],
      timer: '',
      aria2changestatus: {
        gid: '',
        type: ''
      }
    }
  },
  methods: {
    getLocalTime: function (nS) {
      return new Date(nS * 1000).toLocaleString().replace(/:\d{1,2}$/, ' ')
    },
    aria2down: function () {
      let tmp = {
        url: this.textarea.split('\n'),
        path: '/'
      }
      this.$axios({
        url: '/aria2',
        method: 'POST',
        data: tmp
      }).then((res) => {
        tmp = res.data
        if (tmp > 0) {
          this.$message.success('添加成功' + tmp + '个')
        } else {
          this.$message.error('添加失败')
        }
      })
      this.dialogVisible1 = false
    },
    aria2status: function () {
      this.$axios({
        url: '/aria2'
      }).then((res) => {
        this.aria2process = res.data
      })
    },
    size: function (values) {
      var unit = ['B/s', 'KB/s', 'MB/s', 'GB/s', 'TB/s']
      var i = 0
      let value = parseFloat(values)
      while (value >= 1024) {
        value = value / 1024
        i++
      }
      let tmp = value.toFixed(2) + unit[i]
      return (tmp)
    },
    size_: function (values) {
      var unit = ['B', 'KB', 'MB', 'GB', 'TB']
      var i = 0
      let value = parseFloat(values)
      while (value >= 1024) {
        value = value / 1024
        i++
      }
      let tmp = value.toFixed(2) + unit[i]
      return (tmp)
    },
    getpercentnge: function (CompletedLength, TotalLength) {
      if (TotalLength === '0') {
        return 0
      }
      let tmp = Number((CompletedLength / TotalLength * 100).toFixed(2))
      return tmp
    },
    taskcancel: function (item) {
      this.aria2changestatus.gid = item.Gid
      if (item.Status === 'active') {
        this.aria2changestatus.type = 2
      } else if (item.Status === 'removed') {
        this.aria2changestatus.type = 4
      } else {
        this.aria2changestatus.type = 5
      }
      this.$axios({
        method: 'PUT',
        url: '/aria2',
        data: this.aria2changestatus
      }).then((res) => {
        var tmp = res.data
        if (tmp === 'succeed') {
          this.$message.success('任务取消成功')
        } else {
          this.$message.error('任务取消失败')
        }
      })
    },
    taskprocess: function (item) {
      this.aria2changestatus.gid = item.Gid
      if (item.Status === 'active') {
        this.aria2changestatus.type = 1
      } else if (item.Status === 'paused') {
        this.aria2changestatus.type = 3
      }
      this.$axios({
        method: 'PUT',
        url: '/aria2',
        data: this.aria2changestatus
      }).then((res) => {
        var tmp = res.data
        if (tmp === 'succeed') {
          if (item.Status === 'active') {
            this.$message.warning('任务暂停')
          } else {
            this.$message.success('任务继续')
          }
        } else {
          this.$message.error('暂停失败')
        }
      })
    },
    icons: function (item) {
      if (item.Status === 'active') {
        return 'el-icon-video-pause'
      }
      return 'el-icon-video-play'
    },
    status: function (item) {
      if (item.Status === 'active') {
        if (item.CompletedLength === item.TotalLength && item.TotalLength !== '0') {
          return '正在做种'
        }
        return '正在下载'
      } else if (item.Status === 'complete') {
        return '下载完成'
      } else if (item.Status === 'error') {
        return '下载失败'
      } else if (item.Status === 'paused') {
        return '暂停下载'
      } else {
        return '下载移除'
      }
    },
    getflashtime: function () {
      this.$axios.get('/aria2test').then(res => {
        if (res.data !== 'failed') {
          this.aria2status()
          this.timer = setInterval(() => {
            this.aria2status()
          }, res.data.Time * 1000)
        } else {
          this.$message.error('未开启离线下载')
        }
      })
    }
  },
  mounted () {
    this.getflashtime()
  },
  destroyed: function () {
    clearInterval(this.timer)
  },
  beforeCreate () {
    document.title = '离线下载'
  }
}
</script>

<style scoped>
.el-scrollbar__wrap{
  overflow-x: hidden;
}
.bar {
  width: 85%;
  display: inline-block;
}
</style>



