<template>
  <div>
    <el-table
      :data="tableData"
      stripe
      style="width: 100%">
      <el-table-column
        prop="ID"
        label="ID"
        width="60">
      </el-table-column>
      <el-table-column
        prop="Name"
        label="名称">
      </el-table-column>
      <el-table-column
        prop="Type"
        label="类型">
      </el-table-column>
      <el-table-column
        label="已用">
        <template slot-scope="scope">
          {{size_(scope.row.Used)}}
        </template>
      </el-table-column>
      <el-table-column
        label="容量">
        <template slot-scope="scope">
          {{size_(scope.row.Volume)}}
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        width="80">
        <template slot-scope="scope">
          <el-button icon="iconfont icon-lajitong2" circle></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="添加存储方式"
      :visible.sync="dialogVisible"
      :fullscreen = "true"
      center
      >
      <div style="text-align: center;">
        <p>
          <span>储存方式：</span>
          <el-radio v-model="type" label="onedrive">OneDrive</el-radio>
        </p>
        <div v-show="type === 'onedrive'">
          <el-link target="_blank" type="primary" style="margin: 5px;" href="https://portal.azure.com/#blade/Microsoft_AAD_IAM/ActiveDirectoryMenuBlade/Overview" >国际版控制台</el-link>
          <el-link target="_blank" type="primary" style="margin: 5px;" href="https://portal.azure.cn/#blade/Microsoft_AAD_IAM/ActiveDirectoryMenuBlade/Overview" >中国版控制台</el-link>
          <el-tooltip class="item" effect="dark" content="查看帮助" placement="top">
            <el-button icon="iconfont icon-bangzhu" @click="help = !help" type="text" circle></el-button>
          </el-tooltip>
        </div>
        <p>
          <span>储存命名：</span>
          <el-input v-model="name" placeholder="请输入名称" style="width: 150px;"></el-input>
        </p>
        <p>
          <span>容量：</span>
          <el-input v-model="volume" placeholder="请输入容量" style="width: 200px;">
            <el-select v-model="select" slot="append" placeholder="单位" style="width: 80px;">
              <el-option label="KB" value="0"></el-option>
              <el-option label="MB" value="1"></el-option>
              <el-option label="GB" value="2"></el-option>
              <el-option label="TB" value="3"></el-option>
            </el-select>
          </el-input>
        </p>
        <p>
          <span>存储路径：</span>
          <el-input v-model="path_" placeholder="请输入绝对路径" style="width: 200px;"></el-input>
        </p>
        <p v-show="type === 'onedrive'">
          <span>应用ID：</span>
          <el-input v-model="client_id" placeholder="应用程序(客户端) ID" style="width: 200px;"></el-input>
        </p>
        <p v-show="type === 'onedrive'">
          <span>应用KEY：</span>
          <el-input v-model="client_secret" placeholder="客户端密码" style="width: 200px;"></el-input>
        </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addstore">确 定</el-button>
      </span>
    </el-dialog>
    <!--eslint-disable-->
    <el-dialog
      title="OneDrive步骤"
      :visible.sync="help"
      center
      >
      <p><h3>①前往控制台并登录，登录后进入Azure Active Directory管理面板</h3></p>
      <p><h3>②进入左侧 <el-tag>应用注册</el-tag> 菜单，并点击 <el-tag>新注册</el-tag> 按钮</h3></p>
      <p><h3>③填写应用注册表单。其中，名称可任取；<el-tag>受支持的帐户类型</el-tag>选择为 <el-tag>任何组织目录(任何 Azure AD 目录 - 多租户)中的帐户</el-tag>；重定向 URI (可选)请选择Web，并填写<el-tag>{{url}}</el-tag>其他保持默认即可</h3></p>
      <p><h3>④创建完成后进入应用管理的概览页面，<el-tag>复制应用程序(客户端)ID并填写在应用ID </el-tag></h3></p>
      <p><h3>⑤进入应用管理页面左侧的证书和密码菜单，点击新建客户端密码按钮，<el-tag>截止期限选择为从不</el-tag>。创建完成后<el-tag>将客户端密码的值复制填写在应用KEY</el-tag></h3></p>
      <p><h2>至此完成(●ˇ∀ˇ●)</h2></p>
    </el-dialog>
    <el-button @click="dialogVisible=!dialogVisible" style="position:fixed;right: 20px;bottom: 20px;" type="success" icon="iconfont icon-tianjia" circle></el-button>
  </div>
</template>

<script>
/* eslint-disable */
  export default{
    data () {
      return {
        tableData: [],
        dialogVisible: false,
        type: 'onedrive',
        input: '',
        select: '0',
        name: '',
        volume: '',
        path_: '',
        client_id: '',
        client_secret: '',
        help: false,
        url: ''
      }
    },
    methods:{
      addstore: function () {
        if (this.type === 'onedrive') {
          var url
          url = `https://login.microsoftonline.com/common/oauth2/v2.0/authorize?client_id=`+this.client_id+`&scope=files.readwrite.all+offline_access
        &response_type=code&redirect_uri=` + this.url
          this.$cookies.set('name',this.name)
          this.$cookies.set('path_',this.path_)
          this.$cookies.set('volume',parseInt(this.volume)*Math.pow(2,10*this.select))
          this.$cookies.set('client_secret',this.client_secret)
          this.$cookies.set('client_id',this.client_id)
          this.$cookies.set('url',this.url)
          window.open(url)
        } else{
          var pp = new URLSearchParams()
          pp.append('path', this.path_)
          pp.append('name', this.name)
          pp.append('volume', parseInt(this.volume)*Math.pow(2,10*this.select))
          pp.append('type', this.type)
          this.$axios({
            method: 'POST',
            url: '/store',
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: pp
          }).then(
              res => {
                if (res.status == 200) {
                  this.$message.success('添加成功')
                } else {
                  this.$message.error('添加失败')
                }
            })
        }
      },
      size_: function (values) {
        var unit = ['KB', 'MB', 'GB', 'TB']
        var i = 0
        let value = parseFloat(values)
        while (value >= 1024) {
          value = value / 1024
          i++
        }
        let tmp = value.toFixed(2) + unit[i]
        return (tmp)
      }
    },
    beforeMount () {
      this.url = 'https://' + document.domain + '/onedrive'
      this.$axios.get('/store').then(res=>{
        if (res.status == 400) {
          this.$message.error("获取失败")
          return
        }
        this.tableData = res.data
      })
    }
  }
</script>

<style scoped>

</style>



