<template>
  <div>
    <el-table
      :data="usergroup"
      stripe
      style="width: 100%">
      <el-table-column
        prop="GroupID"
        label="ID">
      </el-table-column>
      <el-table-column
        prop="Name"
        label="名称">
      </el-table-column>
      <el-table-column
        prop="StoreID"
        label="储存组ID">
      </el-table-column>
      <el-table-column
        label="总容量">
        <template slot-scope="scope">
          {{size_(scope.row.Volume)}}
        </template>
      </el-table-column>
      <el-table-column
        label="操作">
        <template slot-scope="scope">
          <el-button icon="iconfont icon-bianji_3" @click="editusergroup(scope.row.GroupID,scope.row.Name)" circle></el-button>
          <el-button icon="iconfont icon-lajitong2" @click="deletecheck(scope.row.GroupID)" circle></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="添加用户组"
      :visible.sync="dialogVisible"
      center
      >
    <div style="text-align: center;">
      <p><span>名称：</span>
      <el-input v-model="groupname" placeholder="请输入用户组名称" style="width: 150px;"></el-input>
      </p>
      <p><span>容量：</span>
      <el-input v-model="volume" placeholder="请输入容量" style="width: 200px;">
        <el-select v-model="select" slot="append" placeholder="单位" style="width: 70px;">
          <el-option label="KB" value="0"></el-option>
          <el-option label="MB" value="1"></el-option>
          <el-option label="GB" value="2"></el-option>
          <el-option label="TB" value="3"></el-option>
        </el-select>
      </el-input>
      </p>
      <p>
        <el-tag style="font-size: 15px;margin: 10px;">储存组</el-tag>
        <el-checkbox-group v-model="stores_">
          <el-checkbox v-for="store in stores" :label="store.ID" :key="store.ID">{{store.Name}}</el-checkbox>
        </el-checkbox-group>
      </p>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="addUserGroup()">添加</el-button>
    </span>
    </el-dialog>
    <el-dialog
      title="编辑用户组"
      :visible.sync="dialogVisible_"
      center
      >
    <div style="text-align: center;">
      <p><span>名称：</span>
      <el-input v-model="usergroup_.name" placeholder="请输入用户组名称" style="width: 150px;"></el-input>
      </p>
      <p><span>容量：</span>
      <el-input v-model="usergroup_.volume" placeholder="请输入容量" style="width: 200px;">
        <el-select v-model="select" slot="append" placeholder="单位" style="width: 70px;">
          <el-option label="KB" value="0"></el-option>
          <el-option label="MB" value="1"></el-option>
          <el-option label="GB" value="2"></el-option>
          <el-option label="TB" value="3"></el-option>
        </el-select>
      </el-input>
      </p>
      <p>
        <el-tag style="font-size: 15px;margin: 10px;">储存组</el-tag>
        <el-checkbox-group v-model="usergroup_.storeid">
          <el-checkbox v-for="store in stores" :label="store.ID" :key="store.ID">{{store.Name}}</el-checkbox>
        </el-checkbox-group>
      </p>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible_ = false">取 消</el-button>
      <el-button type="primary" @click="editusergroup_">修改</el-button>
    </span>
    </el-dialog>
    <el-button @click="dialogVisible=!dialogVisible" style="position:fixed;right: 20px;bottom: 20px;" type="success" icon="iconfont icon-tianjia" circle></el-button>
  </div>
</template>

<script>
/* eslint-disable */
export default{
  data () {
    return{
      dialogVisible: false,
      usergroup: [],
      select: '0',
      stores: [],
      stores_: [],
      volume: '',
      groupname: '',
      dialogVisible_: false,
      usergroup_: {
        groupid: '',
        storeid: [],
        volume: '',
        name: ''
      }
    }
  },
  methods: {
    editusergroup_: function () {
      let parmas =new FormData()
      parmas.append('storeid',this.usergroup_.storeid)
      parmas.append('groupid',this.usergroup_.groupid)
      parmas.append('volume',parseInt(this.usergroup_.volume)*Math.pow(2,10*this.select))
      parmas.append('name',this.usergroup_.name)
      this.$axios({
        method: 'PUT',
        url: '/usergroup',
        data: parmas
      }).then(res => {
        if (res.data == "succeed") {
          this.$message.success('修改成功')
        } else {
          this.$message.error('修改失败')
        }
        this.dialogVisible_ = false
      })
    },
    editusergroup: function (groupid, name) {
      this.usergroup_.groupid = groupid
      this.usergroup_.name = name
      this.usergroup_.volume = ''
      this.storeid = ''
      this.dialogVisible_ = true
    },
    size_: function (values) {
      var unit = ['KB', 'MB', 'GB', 'TB']
      var i = 0
      let value = parseFloat(values)
      while (value >= 1024) {
        value = value / 1024
        i++
      }
      let tmp = value.toFixed(2) + unit[i]
      return (tmp)
    },
    addUserGroup: function () {
      var pp = new URLSearchParams()
      pp.append('name',this.groupname)
      pp.append('volume',parseInt(this.volume)*Math.pow(2,10*this.select))
      pp.append('StoreID',this.stores_)
      if (this.groupname === "" || this.volume === "" || this.stores_.length == 0) {
        this.$message.error('请填写所有')
        return
      }
      this.$axios({
        method: 'POST',
        url: '/usergroup',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        },
        data: pp
      }).then(
          res => {
            if (res.status == 200) {
              this.$message.success('添加成功')
            } else {
              this.$message.error('添加失败')
            }
        })
      this.dialogVisible = false
    },
    deletecheck: function (groupid) {
      this.$confirm('此操作将永久删除该组下所有用户及其数据, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
        }).then(() => {
          var pp = new FormData()
          pp.append('groupid',groupid)
          if (groupid == 1) {
            this.$message.error("不可删除此用户组")
            return
          }
          this.$axios({
            method: 'DELETE',
            url: '/usergroup',
            headers: {
              'Content-Type': 'multipart/form-data'
            },
            data: pp
          }).then(
              res => {
                if (res.status == 200) {
                  this.$message.success('删除成功')
                } else {
                  this.$message.error('删除失败')
                }
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        })
    }
  },
  beforeMount () {
    this.$axios.get('/usergroup').then(res=>{
      if (res.status == 400) {
        this.$message.error("获取失败")
        return
      }
      this.usergroup = res.data
    })
    this.$axios.get('/store').then(res=>{
      if (res.status == 400) {
        this.$message.error("获取失败")
        return
      }
      this.stores = res.data
    })
  }
}
</script>

<style>
</style>



