<template>
  <div>
    <el-table
      :data="tableData"
      stripe
      style="width: 100%">
      <el-table-column
        prop="UserID"
        label="ID"
        width="60">
      </el-table-column>
      <el-table-column
        prop="Email"
        label="邮箱"
        width="200">
      </el-table-column>
      <el-table-column
        prop="GroupID"
        label="用户组">
      </el-table-column>
      <el-table-column
        label="注册时间"
        width="160">
        <template slot-scope="scope">
          {{format(scope.row.Time * 1000)}}
        </template>
      </el-table-column>
      <el-table-column
        label="已用"
        width="100">
        <template slot-scope="scope">
          {{size_(scope.row.Used)}}
        </template>
      </el-table-column>
      <el-table-column
        label="容量"
        width="100">
        <template slot-scope="scope">
          {{size_(scope.row.Volume)}}
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        width="120">
        <template slot-scope="scope">
          <el-button icon="iconfont icon-bianji_3" @click="edituser(scope.row)" circle></el-button>
          <el-button icon="iconfont icon-lajitong2" @click="deleteuser(scope.row.Email)" circle></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="添加用户"
      :visible.sync="dialogVisible"
      center
      >
    <div style="text-align: center;">
      <p><span>邮箱：</span>
      <el-input v-model="email" placeholder="请输入邮箱" style="width: 150px;"></el-input>
      </p>
      <p><span>密码：</span>
      <el-input v-model="pw" placeholder="请输入密码" style="width: 150px;"></el-input>
      </p>
      <p><span>用户组：</span>
      <el-select v-model="usergroup" placeholder="请选择" style="width: 150px;">
        <el-option
          v-for="item in options"
          :key="item.GroupID"
          :label="item.Name"
          :value="item.GroupID">
        </el-option>
      </el-select>
      </p>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="adduser">添加</el-button>
    </span>
    </el-dialog>
    <el-dialog
      title="编辑用户"
      :visible.sync="dialogVisible_"
      center
      >
    <div style="text-align: center;">
      <p><span>邮箱：{{user.email}}</span>
      </p>
      <p><span>密码：</span>
      <el-input v-model="user.pw" placeholder="如需修改请输入新密码" style="width: 180px;"></el-input>
      </p>
      <p><span>容量：</span>
      <el-input v-model="user.volume" placeholder="请输入容量" style="width: 200px;">
        <el-select v-model="select" slot="append" placeholder="单位" style="width: 80px;">
          <el-option label="KB" value="0"></el-option>
          <el-option label="MB" value="1"></el-option>
          <el-option label="GB" value="2"></el-option>
          <el-option label="TB" value="3"></el-option>
        </el-select>
      </el-input>
      </p>
      <p><span>用户组：</span>
      <el-select v-model="user.groupid" placeholder="需要修改请选择" style="width: 150px;">
        <el-option
          v-for="item in options"
          :key="item.GroupID"
          :label="item.Name"
          :value="item.GroupID">
        </el-option>
      </el-select>
      </p>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible_ = false">取 消</el-button>
      <el-button type="primary" @click="edituser_">修 改</el-button>
    </span>
    </el-dialog>
    <el-button @click="dialogVisible=!dialogVisible" style="position:fixed;right: 20px;bottom: 20px;" type="success" icon="iconfont icon-tianjia" circle></el-button>
  </div>
</template>

<script>
/* eslint-disable */
export default{
  data () {
    return{
      tableData: [],
      dialogVisible: false,
      email: '',
      pw: '',
      options: [],
      usergroup: '',
      dialogVisible_: false,
      user: {
        email: '',
        groupid: '',
        volume: '',
        pw: '',
        userid: ''
      },
      select: '0'
    }
  },
  methods:{
    edituser_: function () {
      let parmas =new FormData()
      parmas.append('email',this.user.email)
      parmas.append('groupid',this.user.groupid)
      parmas.append('volume',parseInt(this.user.volume)*Math.pow(2,10*this.select))
      parmas.append('pw',this.user.pw)
      parmas.append('userid',this.user.userid)
      this.$axios({
        method: 'PUT',
        url: '/user',
        data: parmas
      }).then(res => {
        if (res.data == "succeed") {
          this.$message.success('修改成功')
        } else {
          this.$message.error('修改失败')
        }
      })
    },
    edituser: function (user) {
      this.user.userid = user.UserID
      this.user.email = user.Email
      this.dialogVisible_ = true
      this.user.groupid = ''
      this.user.volume = ''
      this.pw = ''
    },
    adduser: function () {
      var pp = new URLSearchParams()
      pp.append('Email',this.email)
      pp.append('pw',this.pw)
      pp.append('GroupID',this.usergroup)
      this.$axios({
        url: '/user',
        method: 'POST',
        data: pp,
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(
        res => {
          if (res.status == 200) {
            this.$message.success('添加成功')
          } else {
            this.$message.error('添加失败')
          }
      })
      this.dialogVisible = false
    },
    getLocalTime: function (nS) {
      return new Date(nS * 1000)
    },
    format: function (shijianchuo)
    {
    var time = new Date(shijianchuo);
    var y = time.getFullYear();
    var m = time.getMonth()+1;
    var d = time.getDate();
    var h = time.getHours();
    var mm = time.getMinutes();
    var s = time.getSeconds();
    return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
    },
    add0: function (m){return m<10?'0'+m:m },
    size_: function (values) {
      var unit = ['KB', 'MB', 'GB', 'TB']
      var i = 0
      let value = parseFloat(values)
      while (value >= 1024) {
        value = value / 1024
        i++
      }
      let tmp = value.toFixed(2) + unit[i]
      return (tmp)
    },
    deleteuser: function (item) {
      var pp = new FormData()
      pp.append('email',item)
      this.$axios({
        method: 'DELETE',
        url: '/user',
        headers: {
          'Content-Type': 'multipart/form-data'
        },
        data: pp
      }).then(
          res => {
            if (res.status == 200) {
              this.$message.success('删除成功')
            } else {
              this.$message.error('删除失败')
            }
        })
    }
  },
  beforeMount () {
    this.$axios.get('/user').then(res=>{
      if (res.status == 400) {
        this.$message.error("获取失败")
        return
      }
      this.tableData = res.data
    })
    this.$axios.get('/usergroup').then(res=>{
      if (res.status == 400) {
        this.$message.error("获取失败")
        return
      }
      this.options = res.data
    })
  }
}
</script>

<style>
</style>



