<template>
  <div class="hello">
    <el-container style="min-height:96vh">
      <el-header style="background-color: #f8f8f8;">
        <el-row :gutter="10" style="margin-top: 8px;">
          <el-col :xs="4" :sm="6" :md="4" :lg="3" :xl="1">
            <el-button style="color: #2C3E50;width: 100%;border-radius: 20px;" icon="iconfont icon-wenjian4" @click="goroute(1)"></el-button>
          </el-col>
          <el-col :xs="4" :sm="6" :md="4" :lg="3" :xl="1">
            <el-button style="color: #2C3E50;width: 100%;border-radius: 20px;" icon="iconfont icon-xiazai_2" @click="goroute(2)"></el-button>
          </el-col>
          <el-col :xs="4" :sm="6" :md="4" :lg="3" :xl="1">
            <el-button style="color: #2C3E50;width: 100%;border-radius: 20px;" icon="iconfont icon-fenxiang" @click="goroute(3)"></el-button>
          </el-col>
          <div style="margin-top: 10px;position: fixed;right: 60px;">
            <el-progress :show-text="false" :percentage="percentage" :color="customColor"></el-progress>
            <div style="margin-top: 5px;">{{size(used)}}/{{size(volume)}}</div>
          </div>
          <el-dropdown style="position: fixed;right: 10px;">
          <span class="el-dropdown-link">
            <el-avatar icon="el-icon-user-solid"></el-avatar>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item><el-button v-if="groupid == '1'" @click="goroute(4)" type="text" icon="iconfont icon-shezhi" style="color: #2C3E50;">后台设置</el-button></el-dropdown-item>
            <el-dropdown-item><el-button @click="drawer=true" type="text" icon="iconfont icon-shenfenzheng" style="color: #2C3E50;">个人设置</el-button></el-dropdown-item>
            <el-dropdown-item><el-button @click="logout()" type="text" icon="iconfont icon-tuichudenglu" style="color: #2C3E50;">注销</el-button></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        </el-row>
      </el-header>
      <el-main>
        <router-view></router-view>
      </el-main>
      <el-drawer
        :visible.sync="drawer"
        direction="rtl">
        <div style="margin: 10px;">
          <el-tag>注册日期</el-tag><h3>{{registertime}}</h3>
        </div>
        <div style="margin: 10px;">
          <el-tag type="success">用户组：</el-tag><h3>{{groupname}}</h3>
        </div>
      </el-drawer>
    </el-container>
  </div>
</template>

<script>
import file from './File.vue'
import down from './Down.vue'
import share from './Share.vue'
import playvideo from './video.vue'
import admin from './Admin.vue'

export default {
  name: 'HelloWorld',
  data () {
    return {
      percentage: 0,
      customColor: [
        {color: '#00e100', percentage: 20},
        {color: '#14e1db', percentage: 40},
        {color: '#f5f57a', percentage: 60},
        {color: '#ffaa00', percentage: 80},
        {color: '#f56c6c', percentage: 100}],
      views: file,
      volume: 0,
      used: 0,
      drawer: false,
      groupname: '',
      registertime: '',
      groupid: ''
    }
  },
  components: {
    file,
    down,
    share,
    playvideo,
    admin
  },
  methods: {
    goroute (index) {
      if (index === 1) {
        this.$router.push({path: '/home'})
      } else if (index === 2) {
        this.$router.push({path: '/down'})
      } else if (index === 3) {
        this.$router.push({path: '/share'})
      } if (index === 4) {
        this.$router.push({path: '/admin'})
      }
    },
    size (value) {
      var unit = ['KB', 'MB', 'GB', 'TB']
      var i = 0
      while (value >= 1024) {
        value = value / 1024
        i++
      }
      return (value.toFixed(2) + unit[i])
    },
    logout: function () {
      console.log('')
      this.$cookies.remove('GODKEY')
      this.$message.success('注销成功')
      this.$router.push({path: '/login'})
    }
  },
  beforeMount () {
    this.$axios({
      method: 'POST',
      url: '/cookies'
    }).then(
      response => {
        if (response.data !== 'failed') {
          this.volume = response.data['Volume']
          this.used = response.data['Used']
          this.percentage = (this.used / this.volume) * 100
          this.groupname = response.data.GroupName
          this.groupid = response.data.GroupID
          this.registertime = new Date(response.data.RegisterTime * 1000).toLocaleString().replace(/:\d{1,2}$/, ' ')
        }
      }
    )
    if (this.$route.path === '/') {
      this.$router.push({path: '/home'})
    }
  },
  beforeCreate () {
    if (this.$cookies.get('GODKEY') === null) {
      this.$message.error('未登录')
      this.$router.push({path: '/login'})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-main{
  padding: 5px;
}
.el-button{
  padding: 10px;
}
</style>



