<template>
  <div><d-player ref="player" id="player" :options="options" />
  </div>
</template>

<script>
const Base64 = require('js-base64').Base64
export default{
  data () {
    return {
      options: {
        container: document.getElementById('dplayer'),
        playbackSpeed: [0.5, 0.75, 1, 1.25, 1.5, 2],
        video: {
          url: '',
          thumbnails: ''
        },
        autoplay: true,
        preload: 'metadata'
      }
    }
  },
  beforeMount () {
    this.options.video.url = Base64.decode(this.$route.params.url)
  }
}
</script>

<style>
</style>



