<template>
  <div>
    <h1>请等待。。。。。</h1>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  beforeMount () {
    document.title = 'OneDrive授权'
    if(this.$route.query.code != null) {
      var pp = new URLSearchParams()
      pp.append('client_id',this.$cookies.get('client_id'))
      pp.append('redirect_uri',this.$cookies.get('url'))
      pp.append('client_secret',this.$cookies.get('client_secret'))
      pp.append('code',this.$route.query.code)
      pp.append('grant_type','authorization_code')
      pp.append('name',this.$cookies.get('name'))
      pp.append('path',this.$cookies.get('path_'))
      pp.append('volume',this.$cookies.get('volume'))
      pp.append('type','onedrive')
      this.$axios({
      method: 'POST',
      url: '/store',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      data: pp
    }).then(
        res => {
          if (res.status == 200) {
            this.$message.success('添加成功')
          } else {
            this.$message.error('授权失败')
          }
          this.$cookies.remove('name')
          this.$cookies.remove('path_')
          this.$cookies.remove('volume')
          this.$cookies.remove('client_secret')
          this.$cookies.remove('client_id')
          this.$router.push({path: '/home'})
      })
    }
  }
}
</script>

<style>
</style>



