<template>
  <div>
    <el-tabs style="margin-left: 15px;margin-right: 15px;" v-model="select" @tab-click="$router.push('/admin/'+select)">
        <el-tab-pane label="用户管理" name="user"></el-tab-pane>
        <el-tab-pane label="用户组管理" name="usergroup"></el-tab-pane>
        <el-tab-pane label="储存设置" name="store"></el-tab-pane>
        <el-tab-pane label="离线下载" name="aria2"></el-tab-pane>
    </el-tabs>
    <router-view></router-view>
  </div>
</template>

<script>
import store from './Store.vue'
import user from './User.vue'
import usergroup from './Usergroup.vue'
import aria2 from './Aria2.vue'
export default {
  data () {
    return {
      select: 'first'
    }
  },
  components: {
    store,
    user,
    usergroup,
    aria2
  },
  beforeCreate () {
    if (this.$route.path === '/admin') {
      this.$router.push({path: '/admin/user'})
    }
    document.title = '后台设置'
  }
}
</script>

<style>
</style>



