<template>
    <div class="login-container">
        <el-form :model="ruleForm2" :rules="rules2"
         status-icon
         ref="ruleForm2"
         label-position="left"
         label-width="0px"
         class="demo-ruleForm login-page">
            <h3 class="title">系统登录</h3>
            <el-form-item prop="username">
                <el-input type="text"
                    v-model="ruleForm2.username"
                    auto-complete="off"
                    placeholder="用户名"
                ></el-input>
            </el-form-item>
                <el-form-item prop="password">
                    <el-input type="password"
                        v-model="ruleForm2.password"
                        auto-complete="off"
                        placeholder="密码"
                    ></el-input>
                </el-form-item>
            <el-checkbox
                v-model="ruleForm2.checked"
                class="rememberme"
            >记住密码</el-checkbox>
            <el-form-item style="width:100%;">
                <el-button type="primary" style="width:100%;" @click="handleSubmit" :loading="logining">登录</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
export default {
  data () {
    return {
      logining: false,
      ruleForm2: {
        username: 'admin@godcloud.com',
        password: '123456',
        checked: false
      },
      rules2: {
        username: [{required: true, message: '请输入您的邮箱', trigger: 'blur'}],
        password: [{required: true, message: '请输入您的密码', trigger: 'blur'}]
      }
    }
  },
  methods: {
    handleSubmit (event) {
      let params = new FormData()
      params.append('email', this.ruleForm2.username)
      params.append('pw', this.ruleForm2.password)
      params.append('checked', this.ruleForm2.checked)
      this.$axios({
        method: 'POST',
        url: '/login',
        data: params,
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then(
        response => {
          if (response.data !== 'failed') {
            this.$message.success('登录成功')
            this.$router.push({path: '/home'})
          }
        }
      )
    }
  },
  beforeCreate () {
    document.title = '用户登录'
    if (this.$cookies.get('GODKEY') !== null) {
      this.$router.push({path: '/home'})
    }
  }
}
</script>

<style scoped>
.login-container {
  width: 100%;
  height: 100%;
}
.login-page {
  -webkit-border-radius: 5px;
  border-radius: 20px;
  margin: 180px auto;
  width: 350px;
  padding: 35px 35px 15px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}
label.el-checkbox.rememberme {
  margin: 0px 0px 15px;
  text-align: left;
}
</style>



